.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    backdrop-filter: blur(5px);
    background: rgba(13, 17, 28, 0.5);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;

    &._active2,
    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        max-height: calc(100vh - 3em);
        background: #fff;
        z-index: 3;
        background: linear-gradient(159.80deg, rgb(72, 123, 255) 2.44%,rgb(70, 39, 255) 200.549%);
        padding: .4375em 0 0;
        border-radius: 1em 1em 0 0;
        display: inline-block;
    }
    &__content{
        background: rgb(13, 17, 28);
        border-radius: inherit;
        padding: .75em .75em 1.75em;
    }
    &__btn{
        margin-top: 1.5em;
        width: 100%;
    }
    .close-btn{
        position: absolute;
        right: .75em;
        bottom: 100%;
        margin-bottom: .375em;
    }
}

.close-btn{
    width: 2em;
    @include center;
    opacity: .2;
    &:hover{
        opacity: 1;
    }
    svg{
        width: 100%;
    }
}

.modal,
.increase-profit-modal,
.thank-modal{
    &__info{
        padding: 1.25em .625em;
        background: rgba(22, 27, 39, 0.5);
        border-radius: .75em;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}

.thank-modal{
    .main-sum{
        margin: 0 0 1em;
    }
}

.increase-profit-modal{
    &__info{
        padding-left: 2em;
        padding-right: 2em;
        img{
            width: 6.5em;
            margin-bottom: 1em;
        }
        .text32{
            margin-bottom: 1em;
        }
    }
    &__desc{
        margin-top: 1.5em;
    }
}

.finance-modal,
.chain-modal{
    .modal__dialog{
        padding: 0;
        background: unset;
        border: 1px solid rgba(152, 161, 192, 0.1);
    }
    .modal__content{
        padding: 1.5em .75em;
    }
}

.finance-modal{
    .tabs{
        margin-bottom: 1.5em;
    }
    .wallet{
        margin: 1.5em 0;
    }
    .fg,
    .warning{
        margin-top: .75em;
    }
    &__hint{
        margin-top: 1.5em;
    }
}

.wallet{
    display: grid;
    grid-template-columns: calc(100% - 3.625em) 3.25em;
    gap: .375em;
    &__copy,
    &__code{
        background: #111524;
        border-radius: .75em;
        height: 3.25em;
        display: flex;
        align-items: center;
    }
    &__code{
        padding: 0 1.25em;
    }
    &__copy{
        justify-content: center;
        &:hover{
            g{
                opacity: 1;
            }
        }
    }
}

.coin-field{
    position: relative;
    &__content{
        position: absolute;
        left: .75em;
        top: 50%;
        transform: translateY(-50%);
    }
    &__currency{
        flex-shrink: 0;
        @include center;
        width: 2em;
        height: 2em;
        border-radius: 50%;
        background-color: $white;
        margin-right: .75em;
        img{
            width: 1.375em;
        }
    }
    &__title{
        font-size: 1.25em;
        line-height: 120%;
        font-weight: 500;
    }
    &__bottom{
        margin-top: .125em;
        display: flex;
        align-items: center;
    }
    &__btn{
        margin-left: .375em;
        height: .75em;
        padding: 0 .25em;
        @include center;
        border: 1px solid $gray;
        color: $gray;
        border-radius: 62.5em;
        font-weight: 500;
        span{
            letter-spacing: 0.01em;
            font-size: .375em;
        }
    }
    input{
        width: 100%;
        font-size: 1.75em;
        height: 2.21em;
        border: none;
        background: #111524;
        border-radius: .43em;
        text-align: right;
        padding-right: .43em;
        padding-left: 6.44em;
        color: $white;
        &::placeholder{
            color: $gray;
        }
    }
}

.warning{
    padding: .5em;
    background: linear-gradient(159.80deg, rgba(72, 123, 255, 0.4) 22.887%,rgba(70, 39, 255, 0.4) 105.968%);
    border-radius: .75em;
    display: flex;
    align-items: center;
    img{
        width: 1.5em;
        margin-right: .5em;
    }
}
