.task-card{
  &._done{
    background-color: rgba($gg,.15);
    .task-card__icon{
      background-color: $gg;
      path{
        stroke: $white;
      }
    }
  }
  &__icon svg{
    width: 1.5em;
  }
  .m-coin{
    img{
      margin-left: .25em;
    }
  }
  &__btn{
    margin-left: auto;
    height: 1.5em;
    padding: 0 .625em;
    border: 1px solid $gg;
    border-radius: .375em;
    display: flex;
    align-items: center;
    font-weight: 500;
    img{
      width: .875em;
      margin-left: .25em;
    }
  }
}

.preview-page{
  padding-top: 0;
}

.preview{
  min-height: $vh;
  position: relative;
  @include center;
  flex-direction: column;
  padding: 2em;
  color: $white;
  text-align: center;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    z-index: -1;
  }
  &__title{
    font-size: 2.75em;
    line-height: 110%;
    font-weight: 800;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: .75em;
  }
}
