.history{
  &__items{
    @include grid100gap(.75em);
    max-height: calc(100 * var(--vh) - 9em);
    overflow-y: auto;
  }
}

.history-card{
  padding: .75em;
  border: $border;
  border-radius: .5em;
  @include grid100gap(1em);
  &__row{
    display: flex;
    justify-content: space-between;
  }
  &__title,
  &__desc{
    font-size: .875em;
    line-height: 121%;
  }
  &__title{
    color: $gray;
  }
  &__desc{
    font-weight: 500;
    display: flex;
    align-items: center;
    &._green{
      color: #12FF63;
    }
    img{
      width: 1em;
      margin-right: .28em;
    }
  }
}

.chain-modal{
  &__search{
    margin-top: 1.5em;
    input{
      font-size: 1em;
      height: 3em;
      padding: 0 1em 0 3.5em;
      border-radius: .5em;
    }
    img{
      width: 1.75em;
      position: absolute;
      top: .625em;
      left: 1em;
    }
  }
  &__options{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
  }
  &__option{
    display: flex;
    align-items: center;
    color: $gray;
    transition: all .3s;
    cursor: pointer;
    &._active{
      &:after{
        opacity: 1;
      }
    }
    &._active,
    &:hover{
      color: $white;
    }
    img{
      width: 2em;
      margin-right: .75em;
    }
    &:after{
      content: '';
      flex-shrink: 0;
      margin-left: auto;
      width: 2em;
      height: 2em;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
      opacity: 0;
      transition: all .3s;
    }
  }
}

.chain-select{
  margin-top: .75em;
  padding: .75em;
  display: flex;
  align-items: center;
  background: #111524;
  border-radius: .75em;
  font-weight: 500;
  color: $gray;
  &__icon{
    width: 2em;
    margin-right: .75em;
    border-radius: 50%;
  }
  &__arrow{
    width: 1.25em;
    margin-left: auto;
  }
}
