.main-top{
  &__user{
    &-img{
      border-radius: .5em;
      width: 2.25em;
      margin-right: .5em;
    }
  }
  &__btn{
    color: #487bff;
    padding: 0 1.125em;
    background: rgba(72, 123, 255, 0.3);
    height: 1.75em;
    border-radius: 6.25em;
    @include center;
    span{
      font-size: .75em;
    }
  }
}

._flex-center,
._flex-between,
._flex{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

.main-stat{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .375em));
  gap: .75em;
  margin-top: 1em;
}

.stat-item{
  border-radius: .5em;
  padding: .5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(31, 36, 50, 0.5);
  &__info{
    margin-top: .25em;
    display: flex;
    align-items: center;
  }
  &__coin{
    width: 1.25em;
    margin-right: .25em;
  }
  .hint{
    margin-left: .25em;
  }
}

.hint{
  &__btn{
    width: 1.25em;
    @include center;
    img{
      width: 100%;
    }
  }
}

.main-sum{
  margin-top: 2.5em;
  font-weight: 600;
  &__text{
    font-size: 2.25em;
  }
  img{
    width: 3.5em;
    margin-right: .75em;
  }
}

.main-btn{
  width: 100%;
  max-width: 19.5em;
  margin: 2.25em auto 0;
  position: relative;
  &:before{
    content: '';
    @include centerAbsolute;
    width: 162%;
    height: 162%;
    background-image: url(../img/rounds.svg);
    @include bgImgSize(100%);
  }
  img{
    width: 100%;
  }
  &:active{
    transform: rotate(4deg);
  }
}

.menu{
  position: fixed;
  bottom: .75em;
  left: .75em;
  width: calc(100% - 1.5em);
  background: #161b27;
  padding: .5em;
  border-radius: .75em;
  z-index: 10;
  display: flex;
  &__btn{
    min-height: 3.125em;
    width: calc(20% - .75em / 5);
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    margin-right: .25em;
    background: #1f2432;
    border-radius: .5em;
    padding: .375em;
    color: $gray;
    &:last-child{
      margin-right: 0;
    }
    svg{
      height: 1.5em;
      margin-bottom: .125em;
    }
    span{
      font-size: .6875em;
      letter-spacing: 0.01em;
      line-height: 118%;
    }
    &:hover{
      color: $white;
      path{
        stroke: $white;
      }
    }
    &._active{
      pointer-events: none;
      background: $blueGrad;
      color: $white;
      path{
        stroke: $white;
      }
    }
  }
}

.main-energy{
  position: fixed;
  left: .75em;
  bottom: 5.75em;
  display: flex;
  align-items: center;
  font-weight: 500;
  img{
    width: 1em;
    margin-right: .375em;
  }
}
