@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 3em;
    border-radius: .5em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        letter-spacing: -0.01em;
    }
}

.m-btn-blue{
    background: $blueGrad;
    color: $white;
    &:hover{
        color: $white;
        opacity: .9;
    }
}
