.main-energy,
.main-boost{
  bottom: 7.75em;
}

.main-boost{
  position: fixed;
  right: .75em;
  display: flex;
  align-items: center;
  font-weight: 500;
  img{
    width: 1em;
    margin-right: .375em;
  }
}

.boost{
  &__top{
    text-align: center;
    padding-top: .75em;
    .main-sum{
      margin-top: .25em;
    }
  }
  .tasks{
    margin-top: 2em;
  }
  .task-card{
    &__action{
      display: none;
    }
    .m-coin{
      font-weight: 500;
    }
  }
}

.task-modal2{
  .m-coin{
    font-weight: 500;
    justify-content: center;
  }
  &__wrap{
    padding: 1.25em;
    border: $border;
    border-radius: .5em;
    width: 100%;
    margin-top: .75em;
    text-align: center;
  }
}

.content{
  padding-bottom: 7.5em;
}

.menu{
  bottom: 2.75em;
}

.main-btn{
  max-width: 18em;
  margin-top: 1.25em;
  img{
    position: relative;
    z-index: 2;
  }
}

.main{
  &>.main-sum{
    margin-top: 1em;
  }
}

.referral-action2{
  bottom: 7.5em;
}

.friends-empty{
  height: 8em;
}

.header__icon{
  display: none;
}

.reward-modal{
  .header__icon{
    display: block;
  }
}

