.stat-item__title{
  color: $gold;
}

.main-boost{
  color: $gold;
}

.pagination{
  display: flex;
  justify-content: center;
  margin-top: .75em;
  &__link{
    @include center;
    font-size: .875em;
    width: 2.29em;
    height: 2.29em;
    border-radius: .28em;
    border: 1px solid #DFE3E8;
    margin: 0 .25em;
    &._disabled{
      background-color: #F4F4F4;
      border-color: #F4F4F4;
    }
  }
}

.loader2{
  .loader__bg{
    backdrop-filter: unset;
  }
  .loader__title{
    text-align: center;
    span{
      display: inline-flex;
      border-radius: .22em;
      background-color: $gold;
      padding: 0 .22em;
    }
  }
  .loader__desc{
    position: relative;
    z-index: 2;
    margin-top: 1em;
    text-align: center;
    padding: 0 2em;
  }
}

.mine{
  .main-sum{
    margin-top: 1.25em;
  }
}

.upgrade-modal__desc{
  &._pi16{
    padding-left: 1em;
    padding-right: 1em;
  }
}

.props{
  margin-top: 1.5em;
  @include grid100gap(.75em);
}

.prop-item{
  display: flex;
  justify-content: space-between;
  &__title,
  &__desc{
    font-size: .75em;
    line-height: 125%;
  }
  &__title{
    color: #B0B7B4;
    &--bold{
      color: $black;
      font-weight: 600;
    }
  }
  &__desc{
    font-weight: 600;
  }
}

.props2{
  border-top: 1px solid #F4F4F4;
  padding-top: .75em;
  margin-top: .75em;
  @include grid100gap(.375em);
  overflow: hidden;
}

.progress-bar{
  background-color: rgba($gg,.15);
  height: .875em;
  border-radius: 6.25em;
  position: relative;
  overflow: hidden;
  span{
    max-width: 100%;
    display: block;
    height: 100%;
    background-color: $gg;
  }
}

.success-modal{
  .modal__dialog{
    @include centerAbsolute;
    max-width: calc(100% - 1.5em);
    max-height: unset;
    bottom: unset;
    border: 1px solid rgba(#282928,.1);
    background: #fff;
    border-radius: 1em;
    padding: .75em .75em 1.25em;
    text-align: center;
  }
  .modal__content{
    padding: 0;
    @include grid100gap(1em);
  }
  &__icon{
    width: 3.5em;
    display: block;
    margin: 0 auto;
  }
}

.friends__refresh{
  path{
    stroke: $black;
  }
}

.referral-btn__wrap{
  .m-coin{
    margin-right: .25em;
  }
}

.referral-action{
  margin-top: 1.5em;
}
