.tabs{
  display: flex;
  padding: .375em .5em;
  border: 1px solid rgba(152, 161, 192, 0.1);
  background: #111524;
  border-radius: .5em;
}

.tab{
  border-radius: .25em;
  flex-grow: 1;
  height: 1.75em;
  padding: 0 1em;
  @include center;
  position: relative;
  font-weight: 500;
  color: $gray;
  transition: all .3s;
  span{
    font-size: 1em;
    line-height: 120%;
  }
  &:hover{
    color: $white;
    cursor: pointer;
  }
  &._active{
    background: $blueGrad;
    color: $white;
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
