.wrapper{
  &._type2{

  }
}

.header{
  margin-top: -.25em;
  padding: 1.5em .75em;
  background-image: url(../img/header-bg.svg);
  background-repeat: no-repeat;
  background-position: right top;
  background-size: 10.0625em auto;
  position: relative;
  &__icon{
    position: absolute;
    right: 0;
    top: -1em;
    width: 9.75em;
  }
  &__icon2{
    position: absolute;
    top: -2.875em;
    right: 0;
    width: 8.625em;
  }
  &__text{
    @include grid100gap(.25em);
  }
}

.task-group{
  margin-top: .75em;
  &:first-child{
    margin-top: 0;
  }
  &__items{
    margin-top: .5em;
    @include grid100gap(.25em);
  }
}

.task-card{
  padding: .75em;
  border-radius: .75em;
  background: #161b27;
  display: flex;
  align-items: center;
  &__icon{
    background: #1c222f;
    border-radius: .5em;
    width: 2.25em;
    height: 2.25em;
    @include center;
    flex-shrink: 0;
    margin-right: .75em;
    img{
      width: 1.5em;
    }
  }
  .m-coin{
    margin-top: .25em;
    img{
      width: .875em;
    }
  }
  &__action{
    width: 2em;
    height: 2em;
    margin-left: auto;
    background-image: url(../img/arrow.svg);
    @include bgImgSize(100%);
    flex-shrink: 0;
  }
  &._done{
    .task-card__action{
      background-image: url(../img/check.svg);
    }
  }
}

.task-modal{
  .m-coin{
    margin-top: 1em;
    img{
      width: 1.75em;
    }
  }
  &__do{
    margin-top: 1em;
    height: 2.25em;
    width: 11.25em;
  }
}

.reward-modal{
  .header__icon{
    width: 6.3125em;
    top: -.625em;
    right: 1.875em;
  }
  .header__text{
    max-width: 11.625em;
  }
  .modal__content{
    padding-top: 1.5em;
  }
}

.rewards{
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em / 4));
    gap: .3125em;
  }
}

.reward-card{
  border-radius: .75em;
  border: $border;
  padding: .75em;
  display: flex;
  align-items: center;
  flex-direction: column;
  opacity: 0.5;
  &__icon{
    margin: .25em 0;
    width: 1.75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &._active{
    opacity: 1;
    border-width: 2px;
    border-color: $primary;
  }
  &._done{
    background: $blueGrad;
    border: none;
  }
}

.m-alert{
  position: fixed;
  top: 1em;
  left: 50%;
  transform: translateX(-50%);
  max-width: 98vw;
  padding: 0 .5em;
  background: #14cc8a;
  height: 2.25em;
  display: flex;
  align-items: center;
  border-radius: .5em;
  white-space: nowrap;
  &--red{
    background: #ff1b51;
  }
  &--blue{
    background: $blueGrad ;
  }
  &__icon{
    @include center;
    width: 1.5em;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
}

.m-loader{
  animation: spin 2s infinite linear;
}

@keyframes spin {
  0%  {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}
