.spoiler{
  &:not(._active){
    .spoiler__hidden{
      height: 0;
    }
  }
  &__toggle{
    cursor: pointer;
  }
  &__hidden{
    overflow: hidden;
    transition: all .5s;
  }
}

.faq{
  &__items{
    @include grid100gap(.25em);
  }
}

.qa-card{
  padding: .75em;
  background: #161b27;
  border-radius: .75em;
  &._active{
    .qa-card__btn img{
      transform: rotate(-180deg);
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 2em;
    @include center;
    img{
      width: 100%;
      transition: all .3s;
    }
  }
  .spoiler__wrap{
    padding-top: .375em;
  }
}

.loader{
  position: fixed;
  z-index: 100;
  @include center;
  width: 100%;
  height: $vh;
  left: 0;
  top: 0;
  &__bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    backdrop-filter: blur(5px);
    background: rgba(13, 17, 28, 0.5);
  }
  &__wrap{
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__icon{
    width: 7.5em;
    margin-bottom: .3125em;
    position: relative;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    position: relative;
    z-index: 2;
    font-size: 2.25em;
    line-height: 122%;
    text-transform: uppercase;
    font-weight: 800;
    text-align: center;
  }
}

.loader2{
  align-items: flex-end;
  .loader__wrap{
    width: 100%;
    border-radius: 1.5em 1.5em 0 0;
    background: $blueGrad;
    height: calc(100 * var(--vh) - 4em);
    justify-content: center;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      background-image: url(../img/loader-bg.webp);
      background-size: cover;
      background-position: center;
    }
  }
  .loader__icon{
    width: 5em;
    margin: 1.5em 0 0;
  }
}
