  min-height: calc(100vh - 4em);
}

.content{
  overflow-y: unset;
}

.menu{
  bottom: .5em;
}

.main-boost, .main-energy{
  bottom: 5.5em;
  z-index: 3;
}

.friends__refresh:hover path{
