.success-modal{
  .m-loader{
    width: 3.5em;
    margin: 0 auto;
  }
}

.mine{
  .stat-item{
    background-color: #F4F4F4;
    border: none;
  }
}

.level-tabs{
  display: flex;
  margin: .75em 0;
  padding: .375em .5em;
  background-color: #F4F4F4;
  border-radius: .5em;
  .tab{
    padding: 0;
    width: 25%;
    flex-grow: unset;
    font-weight: 500;
    span{
      font-size: .75em;
    }
  }
}

.tab{
  &:hover{
    color: $gg;
  }
}

.leaderboard{
  .task-group__items{
    padding-bottom: 2.5em;
  }
}

.leaderboard-top{
  display: grid;
  grid-template-columns: calc(50% - 3.75em) 7.5em calc(50% - 3.75em);
  &__img{
    width: 100%;
    display: block;
  }
  &__items{
    text-align: center;
    padding-top: 1em;
    @include grid100gap(1em);
  }
  &__item{
    @include grid100gap(.125em);
  }
}

.stat-item__info2{
  img{
    width: 1.5625em;
    margin-left: -.25em;
    margin-right: .125em;
  }
}

.mine .main-sum2{
  margin-top: 0;
}

.main-sum2{
  position: relative;
  z-index: 2;
  img{
    width: 2.1875em;
    margin-right: .25em;
  }
  .main-sum__text{
    font-size: 1.5em;
  }
}

.task-card{
  .m-coin2{
    img{
      width: 1em;
      margin-right: .125em;
      margin-top: -.125em;
    }
  }
}

.prop-item{
  &__nums{
    display: flex;
  }
  &__num,
  &__num2{
    display: flex;
    align-items: center;
    font-weight: 600;
    img{
      width: .875em;
      margin: 0 .25em;
      &:first-child{
        margin-left: 0;
      }
    }
  }
  &__num2{
    margin-left: .125em;
    img{
      margin-top: -.1875em;
    }
  }
}

.menu__btn{
  ._fill{
    stroke: none;
  }
}

.task-card{
  &._done {
    .task-card__icon{
      ._fill{
        stroke: none;
        fill: $white;
      }
    }
    .task-card__position{
      color: $white;
      background-color: $gg;
    }
  }
  &._my{
    position: fixed;
    width: calc(100% - 1.5em);
    left: .75em;
    bottom: 5em;
    border: 1px solid $gg;
  }
  .m-coin{
    font-weight: 500;
  }
  &__position{
    margin-left: auto;
    padding: 0 .625em;
    @include center;
    height: 1.5em;
    min-width: 1.75em;
    border-radius: .375em;
    border: 1px solid $gg;
    font-weight: 500;
  }
}

.leaderboard-card{
  .task-card__icon{
    svg{
      width: 1.3125em;
    }
  }
}

.yellow-text{
  color: $gold;
}
