$gg: #26a17b;
$bg: #d1d1d1;
$gray: #f4f4f4;
$gray2: #b0b7b4;
$gray3: #fff;
$fon: #fff;
$black: #1e1e1e;
$gold: #C2A970;
.m-page{
  background: $gray;
  color: $black;
  &:after{
    display: none;
  }
}

.wrapper{
  background: $gg;
}

.content{
  background: $fon;
}

.main-top{
  &__btn{
    background-color: rgba($gg,.1);
    color: $gg;
  }
}

.stat-item{
  background: $gray;
}

.gray-text{
  color: $gray2;
}

.menu{
  background: $gray;
  &__btn{
    background: $gray3;
    color: $gray2;
    path{
      stroke: $gray2;
    }
    &._active,
    &:hover{
      background: $gg;
      color: $white;
    }
  }
}

.modal{
  &__dialog{
    background: $gg;
  }
  &__content{
    background: $gray3;
  }
  .close-btn{
    path{
      stroke: $gray2;
    }
  }
}

.increase-profit-modal,
.thank-modal{
  &__info{
    background: $gray;
  }
}

.m-btn-blue{
  background: $gg;
}

.tabs{
  background: $gray;
}

.tab{
  color: $gray2;
  &._active{
    background: $gg;
  }
}

.wallet{
  &__code{
    background: $gray;
  }
  &__copy{
    background: $gray;
  }
}

.coin-field,
.fg{
  input{
    background: $gray;
    color: $black;
    &::placeholder{
      color: $gray2;
    }
  }
}

.coin-field{
  &__btn{
    color: $gray2;
    border-color: $gray2;
  }
}

.chain-select{
  background: $gray;
  color: $gray2;
}

.chain-modal{
  &__option{
    color: $gray2;
    &._active{
      color: $black;
    }
  }
}

.history-card{
  border-color: rgba($gray2,.3);
  &__title{
    color: $gray2;
  }
}

.loader{
  color: $gg;
}

.loader2{
  color: $white;
  .loader__wrap{
    background: unset;
    &:before{
      background-image: url(../img/loader-bg3.webp);
    }
  }
}

.referral-btn{
  background: $gray;
}

.friend-item{
  background: $gray;
}

.friends-empty{
  background: $gray;
}

.header{
  color: $white;
}

.task-card{
  background: $gray;
  &__icon{
    background: $gray3;
  }
}

.upgrade-modal {
  .upgrade-card__icon{
    background: $gray3;
    box-shadow: unset;
  }
  .modal__info{
    background: rgba(#202421, 0.05);
  }
}

.reward-card{
  &._active{
    border-color: $gg;
  }
  &._done{
    background: rgba($gg,.5);
  }
}

.m-alert{
  background: $gg;
  color: $white;
  &--red{
    background: #ff1b51;
  }
  &--yellow{
    background: #f19100;
  }
}

.qa-card{
  background: $gray;
}


