.mine{
  .stat-item{
    background-color: transparent;
    border: $border;
  }
}

.upgrade-profit{
  margin-top: 1.25em;
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .6875em / 2));
    gap: .6875em;
    margin-top: 1em;
  }
}

.upgrade-card{
  padding: .75em;
  border-radius: .75em;
  border: $border;
  &__top{
    display: flex;
  }
  &__icon{
    box-shadow: inset 2px 2px 7px 0 #232836, inset 0 2px 2px 0 #0d111c;
    background: #161b27;
    border-radius: .5em;
    width: 3.375em;
    height: 3.375em;
    @include center;
    flex-shrink: 0;
    margin-right: .75em;
    img{
      width: 2em;
    }
  }
  &__title{
    font-size: .75em;
    line-height: 120%;
    font-weight: 600;
  }
  &__desc{
    font-size: .6875em;
    line-height: 118%;
    color: $gray;
    margin: .33em 0 .18em;
  }
  &__bottom{
    align-items: flex-end;
    display: grid;
    grid-template-columns: 3.375em calc(100% - 4.125em);
    gap: .75em;
    padding-top: .75em;
    margin-top: .75em;
    border-top: 1px solid rgba($gray,.2);
  }
}

.m-coin{
  display: flex;
  align-items: center;
  font-weight: 600;
  img{
    width: 1.25em;
    margin-right: .25em;
  }
}

.upgrade-modal{
  .close-btn{
    bottom: unset;
    margin: 0;
    top: 1.8125em;
    right: 1.375em;
  }
  .upgrade-card__icon{
    width: 4em;
    height: 4em;
    margin: 0 0 1em;
    img{
      width: 2.75em;
    }
    &._type2{
      box-shadow: unset;
      background: transparent;
      img{
        width: 100%;
      }
    }
  }
  &__desc{
    margin-top: 1em;
  }
  &__stat{
    padding: 1.25em;
    margin-top: .75em;
    border-radius: .5em;
    border: $border;
    display: flex;
    flex-direction: column;
    align-items: center;
    &-item{
      margin-top: .25em;
    }
    &-item2{
      margin-top: 1em;
      img{
        width: 1.75em;
      }
    }
  }
}
