.referral-text{
  @include grid100gap(.25em);
}

.referral-action{
  margin-top: .5em;
  @include grid100gap(.25em);
}

.referral-btn{
  padding: .75em;
  background: #161b27;
  border-radius: .75em;
  display: flex;
  align-items: center;
  &__icon{
    width: 3em;
    margin-right: .75em;
  }
  &__wrap{
    margin-top: .25em;
  }
  .m-coin{
    margin-right: .125em;
    img{
      width: .875em;
    }
  }
}

.friends{
  margin-top: .75em;
  &__top{
    margin-bottom: .5em;
  }
  &__refresh{
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__items{
    @include grid100gap(.25em);
  }
}

.friend-item{
  padding: .75em;
  display: flex;
  align-items: center;
  border-radius: .75em;
  background: #161b27;
  &__img{
    width: 2.25em;
    height: 2.25em;
    object-fit: cover;
    border-radius: .5em;
    margin-right: .75em;
  }
  &__wrap{
    margin-top: .25em;
  }
  .m-coin{
    margin-left: .125em;
    img{
      width: .875em;
      margin-right: .25em;
    }
  }
  &__benefit{
    margin-left: auto;
    padding-left: 1em;
  }
}

.friends-empty{
  background: #161b27;
  border-radius: .75em;
  padding: 1.25em;
  @include center;
  flex-direction: column;
  text-align: center;
  height: 18.75em;
  .text12{
    margin-top: .33em;
  }
}

.referral-action2{
  position: fixed;
  bottom: 5.5em;
  left: 0;
  padding: 0 .75em;
  width: 100%;
  display: grid;
  grid-template-columns: calc(100% - 3.5em) 3em;
  gap: .5em;
  &__btn{
    img{
      width: 1.5em;
      margin-right: .5em;
    }
    &._small{
      img{
        margin: 0;
        width: 2em;
      }
    }
  }
}
