@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    height: calc(100 * var(--vh) - 4em);
    flex-shrink: 0;
    background: linear-gradient(159.80deg, rgb(72, 123, 255) 2.44%,rgb(70, 39, 255) 200.549%);
    position: relative;
    z-index: 2;
    padding: .25em 0 0;
    border-radius: 1.5em 1.5em 0 0;
    display: flex;
    flex-direction: column;
}

.content {
    overflow-y: auto;
    overflow-x: hidden;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    background: rgb(13, 17, 28);
    border-radius: inherit;
    padding: 1.25em .75em 5.5em;
}

.m-page {
    padding-top: 4em;
    background: linear-gradient(180.00deg, rgb(32, 39, 56),rgb(7, 8, 22) 100%);
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: #fff;
    font-size: calc(1440px * (16 / 1440));
    position: relative;
    &:after{
        content: '';
        width: 100%;
        height: 11.375em;
        position: absolute;
        left: 0;
        top: 5em;
        box-shadow: 0 4px 200px 0 #483fff;

    }
    @media screen and (max-width: 1440px){
        font-size: calc(100vw * (16 / 1440));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 .75em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 375));
    }
}


